import { React } from 'react';
import css from './SearchForm.module.css';
import Input from '../../Input';
import Button from '../../Button';
import Checkbox from '../../CheckBox';
import { useForm } from "react-hook-form";
import { useDispatch, useSelector, shallowEqual } from 'react-redux';
import histogrReq from '../../../utils/histogramsRequest';
import useFetchData from '../../../hooks/useFetchData.js';
import uniqid from 'uniqid';
import DatePick from '../../DatePick';
import validate from '../../../utils/validateInn.js';

const SearchForm = () => {
    const {fetch} = useFetchData();
    const selectedData = useSelector((state) => state, shallowEqual);
    const dispatch = useDispatch();
    const token = selectedData.login.token.accessToken;

    const { register, formState: { errors, isValid }, handleSubmit, control, setError } = useForm({ mode: 'onChange' });

    const onSubmit = data => {
        if (data.dateFrom.$d.getTime() - data.dateTo.$d.getTime() > 0) {
            setError("dateFrom", { type: "required" }, { required: true });
            return;
        }

        dispatch({ type: 'IS_LOADING', payload: true })
        const req = histogrReq(data)
        fetch('/api/v1/objectsearch/histograms', req, token, 'searchInstance', 'histoGram');
        fetch('/api/v1/objectsearch', req, token, 'searchInstance', 'idList');
    }

    const checkboxConfig = [
        '  ',
        '  -',
        '   ',
        '   -',
        '   ',
        '   ',
        '  '
    ]
    
    const renderAlert = (name) => {
        if(errors[name]?.type === 'required' ||  errors[name]?.type === 'validate') {
            return <p className={css.alert} role="alert">  </p>
        } else return <p className={css.isInvisible}>  </p>
    }

    return (
        <>
            <form className={css.form} onSubmit={handleSubmit(onSubmit)}>
                <div className={css.formLeft}>
                    <div className={css.formLeftMain}>
                        <label htmlFor="inn">  *</label>
                        <Input
                            register={register}
                            name="inn"
                            required
                            type="text"
                            placeholder={'10 '}
                            validate={validate}
                        />
                        {renderAlert('inn')}
                        <label className={css.selectLbl} htmlFor="tone"></label>
                        <select
                            {...register("tone", { required: false })}
                            className={css.select}
                            name="tone"
                        >
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                        <label className={css.qtyLbl} htmlFor="qty">   *</label>
                        <Input
                            register={register}
                            name="qty"
                            required
                            type="text"
                            placeholder={' 1  1000'}
                        />
                        {renderAlert('qty')}
                    </div>
                    <div className={css.formLeftBottom}>
                        <label htmlFor="cb1"> *</label>
                        <div className={css.formLeftdate}>
                            <DatePick 
                                control={control}
                                name={'dateFrom'}
                                placeholder={' '}
                            />
                            <DatePick 
                                control={control}
                                name={'dateTo'}
                                placeholder={' '}
                            />
                        </div>
                    </div>
                    {renderAlert('dateFrom')}
                    <div className={css.btnDivMobile}>
                        <Button
                            type={'submit'}
                            disabled={!isValid}
                            fontColor={'white'}
                            bgColor={'bgBlue'}
                        >
                            
                        </Button>
                        <p className={css.comment}>*    </p>
                    </div>
                </div>
                <div className={css.formRight}>
                    <div className={css.checkboxes}>
                        {
                            checkboxConfig.map((item, index) => {
                                return <Checkbox
                                    key={uniqid()}
                                    register={register}
                                    label={item}
                                    name={`cb${index + 1}`}
                                    required={false}
                                />
                            })
                        }
                    </div>
                    <div className={css.formRightBottom}>
                        <div className={css.btnDiv}>
                            <Button
                                type={'submit'}
                                disabled={!isValid}
                                fontColor={'white'}
                                bgColor={'bgBlue'}
                            >
                                
                            </Button>
                            <p className={css.comment}>*    </p>
                        </div>
                    </div>
                </div>
            </form>
        </>
    )
}

export default SearchForm;